body{
  font-family: Arial, serif;
  font-size: 16px;
  line-height: 18px;
  .header {
    &__nav{
      .container {
        .row {
          .col-lg-1 {
            ul{
              li{
                a {
                  padding: 0 5px;
                  display: flex;
                  align-items: center;
                  position: relative;
                  font: .875em/50px "Arial";
                  color: #777a79;
                  text-decoration: none;
                  z-index: 10;
                }
                ul {
                  display: none;
                  flex-direction: column;
                  position: absolute;
                  top: 40px;
                  background: #fff;
                  z-index: 10;
                  li{
                    a{
                      line-height: 30px;
                    }
                  }
                }
              }
            }
          }
          .col-lg-5 {
            ul {
              display: flex;
              justify-content: space-between;
              li {
                a {
                  display: flex;
                  align-items: center;
                  font: .875em/50px "Arial";
                  color: #777a79;
                  text-decoration: none;
                  &:hover{
                    text-decoration: underline;
                  }
                }
              }
            }
            .mobile__nav{
              display: none;
              position: relative;
              left: -15px;
              text-align: center;
              #mobileNav,
              #mobileNavClose{
                font-size: 1.5em;
                line-height: 50px;
                color: #777a79;
                text-decoration: none;
              }
              ul{
                width: 200px;
                flex-direction: column;
                position: absolute;
                top: 50px;
                right: -35px;
                background-color: #fff;
                box-shadow: 5px 5px 15px #c4c4c4;
                z-index: 20;
                li{
                  a{
                    padding: 0 0 0 25px;
                    border-top: 1px solid #c4c4c4;
                    border-bottom: 1px solid #c4c4c4;
                  }
                }
              }
            }
          }
        }
      }
    }
    &__headline{
      height: 135px;
      background-color: #e6e6e6;
      .container{
        .row{
          .col-lg-12{
            p {
              font: 8.9em/135px "BravoSC";
              color: #eaeaea;
              text-align: center;
              position: relative;
            }
          }
          .col-lg-6{
            .logo {
              width: 100%;
              height: 135px;
              display: flex;
              justify-content: center;
              align-items: center;
              position: absolute;
              top: -135px;
              z-index: 5;
              a {
                width: 310px;
                height: 135px;
                display: block;
                background-image: url("../img/logo.png");
                background-size: 100% 100%;
              }
              div {
                position: relative;
                left: -15px;
                p {
                  &:nth-child(1) {
                    margin: 0 0 0 20px;
                    font: 1.188em/1em "BravoSC";
                    color: #00548f;
                    text-align: left;
                  }
                  &:nth-child(2) {
                    font: 1.5em/1em "BravoSC";
                    color: #00548f;
                    text-align: left;
                  }
                }
              }
            }
            .search{
              width: 100%;
              height: 135px;
              display: flex;
              justify-content: flex-end;
              align-items: center;
              position: absolute;
              top: -135px;
              z-index: 5;
              input{
                width: 460px;
                height: 33px;
                padding: 0 0 0 15px;
                font: .875em/33px "Arial";
                color: #9fa1a2;
                background-color: #f5f5f5;
                border: 1px solid #c4c4c4;
              }
              i{
                width: 37px;
                height: 33px;
                line-height: 33px;
                text-align: center;
                background: #fff;
                border-top: 1px solid #c4c4c4;
                border-right: 1px solid #c4c4c4;
                border-bottom: 1px solid #c4c4c4;
              }
            }
          }
        }
      }
    }
    &__mainNav{
      background-color: #009fff;
      .container{
        .row{
          .col-lg-12{
            ul{
              display: flex;
              justify-content: space-between;
              li{
                position: relative;
                a{
                  height: 60px;
                  padding: 0 5px;
                  display: flex;
                  flex-direction: column;
                  justify-content: center;
                  align-items: center;
                  position: relative;
                  text-decoration: none;
                  span{
                    display: block;
                    font: .813em/1.063em "Arial";
                    text-align: center;
                    text-transform: uppercase;
                    &:nth-child(1){
                      font-weight: bold;
                      color: #fff;
                    }
                    &:nth-child(2){
                      color: #9fdbff;
                    }
                  }
                  &:hover{
                    background-color: #007eec;
                  }
                }
              }
              .active{
                background-color: #007eec;
                &:after{
                  content: "";
                  width: 100%;
                  height: 0;
                  position: absolute;
                  top: 100%;
                  left: 0;
                  border: 90px solid transparent;
                  border-top: 18px solid #007eec;
                }
              }
            }
            .mobile__mainNav{
              display: none;
              justify-content: space-between;
              position: relative;
              #mobileMainNav,
              #mobileMainNavClose{
                padding: 0 5px;
                font-size: 1.5em;
                line-height: 60px;
                color: #fff;
                text-decoration: none;
                &:hover{
                  cursor: pointer;
                }
              }
              ul{
                width: 300px;
                flex-direction: column;
                position: absolute;
                top: 60px;
                left: -15px;
                background-color: #009fff;
                z-index: 20;
              }
              .search{
                width: 100%;
                display: flex;
                justify-content: flex-end;
                align-items: center;
                input{
                  width: 90%;
                  height: 33px;
                  padding: 0 0 0 15px;
                  font: .875em/33px "Arial";
                  color: #9fa1a2;
                  background-color: #f5f5f5;
                  border: 1px solid #c4c4c4;
                }
                i{
                  width: 37px;
                  height: 33px;
                  line-height: 33px;
                  text-align: center;
                  background: #fff;
                  border-top: 1px solid #c4c4c4;
                  border-right: 1px solid #c4c4c4;
                  border-bottom: 1px solid #c4c4c4;
                }
              }
            }
          }
        }
      }
    }
  }

  #overlay {
    z-index: 150;
    position: fixed;
    background-color: #000;
    opacity: .7;
    filter: alpha(opacity=80);
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
    cursor: pointer;
    display: none;
  }
}